import React from "react";
import { AnimatePresence, motion } from "framer-motion";
import { MessageCircle, X } from "lucide-react";
import { AvatarImg } from "./AvatarImg";

export function Launcher({
  open,
  onToggle,
  avatarImageUrl,
}: {
  open: boolean;
  onToggle: () => void;
  avatarImageUrl: string;
}) {
  return (
    <div className="pointer-events-auto fixed bottom-6 right-6 z-[61]">
      <AnimatePresence>{!open && <LauncherCard onClick={onToggle} avatarImageUrl={avatarImageUrl} />}</AnimatePresence>
      <AnimatePresence>
        {open && (
          <motion.button
            key="launcher-close"
            aria-label="Close chat"
            onClick={onToggle}
            className="h-12 w-12 rounded-full grid place-items-center bg-[var(--aether-primary)] text-white shadow-xl"
            initial={{ scale: 0.85, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            exit={{ scale: 0.85, opacity: 0 }}
            whileTap={{ scale: 0.96 }}
          >
            <X className="h-5 w-5" />
          </motion.button>
        )}
      </AnimatePresence>
    </div>
  );
}

function LauncherCard({ onClick, avatarImageUrl }: { onClick: () => void; avatarImageUrl: string }) {
  return (
    <motion.button
      key="launcher-card"
      onClick={onClick}
      className="relative flex items-center gap-3 rounded-2xl bg-white dark:bg-zinc-900 shadow-2xl px-3 pr-14 py-3 text-left border border-zinc-200/70 dark:border-white/10"
      initial={{ opacity: 0, y: 8, scale: 0.98 }}
      animate={{ opacity: 1, y: 0, scale: 1 }}
      exit={{ opacity: 0, y: 8, scale: 0.98 }}
      whileHover={{ y: -1 }}
    >
      <AvatarImg src={avatarImageUrl} alt="Avatar" className="h-10 w-10 rounded-full" />
      <div className="max-w-[240px]">
        <p className="text-sm font-semibold text-zinc-900 dark:text-white">Letâs Chat, Your Way</p>
        <p className="text-xs text-zinc-600 dark:text-zinc-300">
          This chatbot adapts to your style of conversation.
        </p>
      </div>
      <motion.span
        className="absolute right-3 top-1/2 -translate-y-1/2 h-9 w-9 rounded-full grid place-items-center text-white bg-[var(--aether-primary)] shadow"
        animate={{ scale: [1, 1.05, 1] }}
        transition={{ repeat: Infinity, duration: 3.2 }}
      >
        <MessageCircle className="h-4 w-4" />
      </motion.span>
      {/* gentle outward glow pulse */}
      <motion.span
        aria-hidden
        className="pointer-events-none absolute inset-0 rounded-2xl"
        animate={{
          boxShadow: [
            "0 0 0 0 rgba(124,58,237,0.0)",
            "0 0 0 10px rgba(124,58,237,0.10)",
            "0 0 0 0 rgba(124,58,237,0.0)",
          ],
        }}
        transition={{ duration: 4.5, repeat: Infinity, repeatDelay: 6 }}
      />
    </motion.button>
  );
}
